.Searchbar {
    position: fixed;
    width: 100vw;
    font-size: 1.5em;
    border: solid black 1px;

    -webkit-animation-name: testAnim;

            animation-name: testAnim;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}


.Searchbar-visible {
    top: 0; 
}

.Searchbar-hidden {
    top: -1.5em; 
}


